'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Textarea } from '@/components/ui/textarea'
import { Badge } from '@/components/ui/badge'
import { Separator } from '@/components/ui/separator'
import { ejerciciosBase, categorias, type Ejercicio } from '@/lib/datos-ejercicios'

interface EjercicioRutina {
  id: string
  ejercicio: Ejercicio
  series: number
  repeticiones: string
  peso: string
  descanso: string
  notas?: string
}

interface FormularioRutinaProps {
  onGuardar: (rutina: any) => void
  rutinaInicial?: any
}

export default function FormularioRutina({ onGuardar, rutinaInicial }: FormularioRutinaProps) {
  const [nombreRutina, setNombreRutina] = useState(rutinaInicial?.nombre || '')
  const [descripcion, setDescripcion] = useState(rutinaInicial?.descripcion || '')
  const [ejercicios, setEjercicios] = useState<EjercicioRutina[]>(rutinaInicial?.ejercicios || [])
  const [ejercicioSeleccionado, setEjercicioSeleccionado] = useState<string>('')
  const [categoriaFiltro, setCategoriaFiltro] = useState<string>('todas')
  const [busqueda, setBusqueda] = useState('')

  // Filtrar ejercicios
  const ejerciciosFiltrados = ejerciciosBase.filter(ejercicio => {
    const coincideBusqueda = ejercicio.nombre.toLowerCase().includes(busqueda.toLowerCase()) ||
                            ejercicio.musculosPrincipales.some(musculo => 
                              musculo.toLowerCase().includes(busqueda.toLowerCase())
                            )
    const coincideCategoria = categoriaFiltro === 'todas' || ejercicio.categoria === categoriaFiltro
    
    return coincideBusqueda && coincideCategoria
  })

  const agregarEjercicio = () => {
    if (!ejercicioSeleccionado) return

    const ejercicio = ejerciciosBase.find(e => e.id === ejercicioSeleccionado)
    if (!ejercicio) return

    const nuevoEjercicio: EjercicioRutina = {
      id: Date.now().toString(),
      ejercicio,
      series: 3,
      repeticiones: '10',
      peso: '0',
      descanso: '60',
      notas: ''
    }

    setEjercicios([...ejercicios, nuevoEjercicio])
    setEjercicioSeleccionado('')
  }

  const actualizarEjercicio = (id: string, campo: keyof EjercicioRutina, valor: any) => {
    setEjercicios(ejercicios.map(ej => 
      ej.id === id ? { ...ej, [campo]: valor } : ej
    ))
  }

  const eliminarEjercicio = (id: string) => {
    setEjercicios(ejercicios.filter(ej => ej.id !== id))
  }

  const guardarRutina = () => {
    if (!nombreRutina.trim()) {
      alert('Por favor, ingresa un nombre para la rutina')
      return
    }

    if (ejercicios.length === 0) {
      alert('Por favor, agrega al menos un ejercicio')
      return
    }

    const rutina = {
      id: rutinaInicial?.id || Date.now().toString(),
      nombre: nombreRutina,
      descripcion,
      ejercicios,
      fechaCreacion: rutinaInicial?.fechaCreacion || new Date().toISOString(),
      fechaModificacion: new Date().toISOString()
    }

    onGuardar(rutina)
  }

  return (
    <div className="space-y-6">
      {/* Información básica de la rutina */}
      <Card>
        <CardHeader>
          <CardTitle>Información de la Rutina</CardTitle>
          <CardDescription>
            Define los detalles básicos de tu rutina de entrenamiento
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          <div>
            <Label htmlFor="nombre">Nombre de la rutina *</Label>
            <Input
              id="nombre"
              value={nombreRutina}
              onChange={(e) => setNombreRutina(e.target.value)}
              placeholder="Ej: Rutina de Pecho y Tríceps"
            />
          </div>
          <div>
            <Label htmlFor="descripcion">Descripción (opcional)</Label>
            <Textarea
              id="descripcion"
              value={descripcion}
              onChange={(e) => setDescripcion(e.target.value)}
              placeholder="Describe el objetivo de esta rutina..."
              rows={3}
            />
          </div>
        </CardContent>
      </Card>

      {/* Agregar ejercicios */}
      <Card>
        <CardHeader>
          <CardTitle>Agregar Ejercicios</CardTitle>
          <CardDescription>
            Busca y selecciona ejercicios para tu rutina
          </CardDescription>
        </CardHeader>
        <CardContent className="space-y-4">
          {/* Filtros */}
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <Label htmlFor="busqueda">Buscar ejercicio</Label>
              <Input
                id="busqueda"
                value={busqueda}
                onChange={(e) => setBusqueda(e.target.value)}
                placeholder="Buscar por nombre o músculo..."
              />
            </div>
            <div>
              <Label htmlFor="categoria">Categoría</Label>
              <Select value={categoriaFiltro} onValueChange={setCategoriaFiltro}>
                <SelectTrigger>
                  <SelectValue placeholder="Seleccionar categoría" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="todas">Todas las categorías</SelectItem>
                  {categorias.map(categoria => (
                    <SelectItem key={categoria.id} value={categoria.id}>
                      {categoria.nombre}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
          </div>

          {/* Selector de ejercicio */}
          <div className="flex gap-2">
            <div className="flex-1">
              <Select value={ejercicioSeleccionado} onValueChange={setEjercicioSeleccionado}>
                <SelectTrigger>
                  <SelectValue placeholder="Seleccionar ejercicio" />
                </SelectTrigger>
                <SelectContent>
                  {ejerciciosFiltrados.map(ejercicio => (
                    <SelectItem key={ejercicio.id} value={ejercicio.id}>
                      <div className="flex items-center gap-2">
                        <span>{ejercicio.nombre}</span>
                        <Badge variant="secondary" className="text-xs">
                          {ejercicio.categoria}
                        </Badge>
                      </div>
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </div>
            <Button onClick={agregarEjercicio} disabled={!ejercicioSeleccionado}>
              Agregar
            </Button>
          </div>
        </CardContent>
      </Card>

      {/* Lista de ejercicios agregados */}
      {ejercicios.length > 0 && (
        <Card>
          <CardHeader>
            <CardTitle>Ejercicios de la Rutina ({ejercicios.length})</CardTitle>
            <CardDescription>
              Configura las series, repeticiones y pesos para cada ejercicio
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              {ejercicios.map((ejercicioRutina, index) => (
                <div key={ejercicioRutina.id} className="border rounded-lg p-4">
                  <div className="flex items-start justify-between mb-3">
                    <div>
                      <h4 className="font-semibold">{ejercicioRutina.ejercicio.nombre}</h4>
                      <div className="flex gap-2 mt-1">
                        <Badge variant="outline">
                          {ejercicioRutina.ejercicio.categoria}
                        </Badge>
                        <Badge variant="outline">
                          {ejercicioRutina.ejercicio.dificultad}
                        </Badge>
                      </div>
                      <p className="text-sm text-gray-600 mt-1">
                        {ejercicioRutina.ejercicio.musculosPrincipales.join(', ')}
                      </p>
                    </div>
                    <Button
                      variant="destructive"
                      size="sm"
                      onClick={() => eliminarEjercicio(ejercicioRutina.id)}
                    >
                      Eliminar
                    </Button>
                  </div>

                  <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                    <div>
                      <Label htmlFor={`series-${ejercicioRutina.id}`}>Series</Label>
                      <Input
                        id={`series-${ejercicioRutina.id}`}
                        type="number"
                        min="1"
                        value={ejercicioRutina.series}
                        onChange={(e) => actualizarEjercicio(ejercicioRutina.id, 'series', parseInt(e.target.value))}
                      />
                    </div>
                    <div>
                      <Label htmlFor={`repeticiones-${ejercicioRutina.id}`}>Repeticiones</Label>
                      <Input
                        id={`repeticiones-${ejercicioRutina.id}`}
                        value={ejercicioRutina.repeticiones}
                        onChange={(e) => actualizarEjercicio(ejercicioRutina.id, 'repeticiones', e.target.value)}
                        placeholder="Ej: 8-12"
                      />
                    </div>
                    <div>
                      <Label htmlFor={`peso-${ejercicioRutina.id}`}>Peso (kg)</Label>
                      <Input
                        id={`peso-${ejercicioRutina.id}`}
                        value={ejercicioRutina.peso}
                        onChange={(e) => actualizarEjercicio(ejercicioRutina.id, 'peso', e.target.value)}
                        placeholder="0"
                      />
                    </div>
                    <div>
                      <Label htmlFor={`descanso-${ejercicioRutina.id}`}>Descanso (seg)</Label>
                      <Input
                        id={`descanso-${ejercicioRutina.id}`}
                        type="number"
                        value={ejercicioRutina.descanso}
                        onChange={(e) => actualizarEjercicio(ejercicioRutina.id, 'descanso', e.target.value)}
                      />
                    </div>
                  </div>

                  <div className="mt-3">
                    <Label htmlFor={`notas-${ejercicioRutina.id}`}>Notas (opcional)</Label>
                    <Input
                      id={`notas-${ejercicioRutina.id}`}
                      value={ejercicioRutina.notas || ''}
                      onChange={(e) => actualizarEjercicio(ejercicioRutina.id, 'notas', e.target.value)}
                      placeholder="Notas sobre técnica, sensaciones, etc."
                    />
                  </div>

                  {index < ejercicios.length - 1 && <Separator className="mt-4" />}
                </div>
              ))}
            </div>
          </CardContent>
        </Card>
      )}

      {/* Botones de acción */}
      <div className="flex gap-3 justify-end">
        <Button variant="outline" onClick={() => window.history.back()}>
          Cancelar
        </Button>
        <Button onClick={guardarRutina}>
          {rutinaInicial ? 'Actualizar Rutina' : 'Guardar Rutina'}
        </Button>
      </div>
    </div>
  )
}
