'use client'

import { useState, useEffect } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs'
import { Input } from '@/components/ui/input'
import { Label } from '@/components/ui/label'
import FormularioRutina from '@/components/FormularioRutina'
import RutinasSugeridas from '@/components/RutinasSugeridas'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'

interface Rutina {
  id: string
  nombre: string
  descripcion: string
  ejercicios: any[]
  fechaCreacion: string
  fechaModificacion: string
  vecesRealizada?: number
}

export default function PaginaRutinas() {
  const [rutinas, setRutinas] = useState<Rutina[]>([])
  const [rutinaEditando, setRutinaEditando] = useState<Rutina | null>(null)
  const [mostrarFormulario, setMostrarFormulario] = useState(false)
  const [busqueda, setBusqueda] = useState('')
  const [rutinaSeleccionada, setRutinaSeleccionada] = useState<Rutina | null>(null)

  useEffect(() => {
    cargarRutinas()
  }, [])

  const cargarRutinas = () => {
    try {
      const rutinasGuardadas = localStorage.getItem('rutinas')
      if (rutinasGuardadas) {
        setRutinas(JSON.parse(rutinasGuardadas))
      }
    } catch (error) {
      console.error('Error cargando rutinas:', error)
    }
  }

  const guardarRutina = (rutina: Rutina) => {
    try {
      let rutinasActualizadas
      
      if (rutinaEditando) {
        // Actualizar rutina existente
        rutinasActualizadas = rutinas.map(r => r.id === rutina.id ? rutina : r)
      } else {
        // Agregar nueva rutina
        rutinasActualizadas = [...rutinas, rutina]
      }

      setRutinas(rutinasActualizadas)
      localStorage.setItem('rutinas', JSON.stringify(rutinasActualizadas))
      
      setMostrarFormulario(false)
      setRutinaEditando(null)
      
      alert('Rutina guardada exitosamente!')
    } catch (error) {
      console.error('Error guardando rutina:', error)
      alert('Error al guardar la rutina')
    }
  }

  const crearRutinaDesdeTemplate = (rutinaPredefinida: any) => {
    try {
      const rutinasActualizadas = [...rutinas, rutinaPredefinida]
      setRutinas(rutinasActualizadas)
      localStorage.setItem('rutinas', JSON.stringify(rutinasActualizadas))
      alert(`¡Rutina "${rutinaPredefinida.nombre}" agregada a tus rutinas!`)
    } catch (error) {
      console.error('Error guardando rutina predefinida:', error)
      alert('Error al guardar la rutina')
    }
  }

  const eliminarRutina = (id: string) => {
    if (confirm('¿Estás seguro de que quieres eliminar esta rutina?')) {
      const rutinasActualizadas = rutinas.filter(r => r.id !== id)
      setRutinas(rutinasActualizadas)
      localStorage.setItem('rutinas', JSON.stringify(rutinasActualizadas))
    }
  }

  const editarRutina = (rutina: Rutina) => {
    setRutinaEditando(rutina)
    setMostrarFormulario(true)
  }

  const duplicarRutina = (rutina: Rutina) => {
    const rutinaDuplicada = {
      ...rutina,
      id: Date.now().toString(),
      nombre: `${rutina.nombre} (Copia)`,
      fechaCreacion: new Date().toISOString(),
      fechaModificacion: new Date().toISOString(),
      vecesRealizada: 0
    }
    
    const rutinasActualizadas = [...rutinas, rutinaDuplicada]
    setRutinas(rutinasActualizadas)
    localStorage.setItem('rutinas', JSON.stringify(rutinasActualizadas))
  }

  const marcarRutinaRealizada = (id: string) => {
    const rutinasActualizadas = rutinas.map(rutina => {
      if (rutina.id === id) {
        return {
          ...rutina,
          vecesRealizada: (rutina.vecesRealizada || 0) + 1,
          fechaModificacion: new Date().toISOString()
        }
      }
      return rutina
    })
    
    setRutinas(rutinasActualizadas)
    localStorage.setItem('rutinas', JSON.stringify(rutinasActualizadas))
    
    // También guardar en historial de entrenamientos
    const historial = JSON.parse(localStorage.getItem('historialEntrenamientos') || '[]')
    const rutina = rutinas.find(r => r.id === id)
    
    if (rutina) {
      historial.push({
        id: Date.now().toString(),
        rutinaId: id,
        nombreRutina: rutina.nombre,
        fecha: new Date().toISOString(),
        ejercicios: rutina.ejercicios
      })
      localStorage.setItem('historialEntrenamientos', JSON.stringify(historial))
    }
    
    alert('¡Entrenamiento registrado!')
  }

  const rutinasFiltradas = rutinas.filter(rutina =>
    rutina.nombre.toLowerCase().includes(busqueda.toLowerCase()) ||
    rutina.descripcion.toLowerCase().includes(busqueda.toLowerCase())
  )

  const nuevaRutina = () => {
    setRutinaEditando(null)
    setMostrarFormulario(true)
  }

  if (mostrarFormulario) {
    return (
      <div className="space-y-6">
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold text-gray-900">
              {rutinaEditando ? 'Editar Rutina' : 'Nueva Rutina'}
            </h1>
            <p className="text-gray-600 mt-1">
              {rutinaEditando ? 'Modifica tu rutina existente' : 'Crea una rutina personalizada'}
            </p>
          </div>
          <Button 
            variant="outline" 
            onClick={() => {
              setMostrarFormulario(false)
              setRutinaEditando(null)
            }}
          >
            Volver a Rutinas
          </Button>
        </div>

        <FormularioRutina 
          onGuardar={guardarRutina}
          rutinaInicial={rutinaEditando}
        />
      </div>
    )
  }

  return (
    <div className="space-y-6">
      {/* Encabezado */}
      <div className="flex flex-col md:flex-row md:items-center md:justify-between">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Mis Rutinas</h1>
          <p className="text-gray-600 mt-1">
            Gestiona y ejecuta tus rutinas de entrenamiento
          </p>
        </div>
        <Button onClick={nuevaRutina} className="mt-4 md:mt-0">
          Nueva Rutina
        </Button>
      </div>

      <Tabs defaultValue="mis-rutinas" className="space-y-6">
        <TabsList>
          <TabsTrigger value="mis-rutinas">Mis Rutinas ({rutinas.length})</TabsTrigger>
          <TabsTrigger value="sugerencias">Rutinas Sugeridas</TabsTrigger>
        </TabsList>

        <TabsContent value="mis-rutinas" className="space-y-6">
          {/* Buscador */}
          {rutinas.length > 0 && (
            <div className="max-w-md">
              <Label htmlFor="busqueda">Buscar rutinas</Label>
              <Input
                id="busqueda"
                value={busqueda}
                onChange={(e) => setBusqueda(e.target.value)}
                placeholder="Buscar por nombre o descripción..."
              />
            </div>
          )}

          {/* Lista de rutinas */}
          {rutinasFiltradas.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
              {rutinasFiltradas.map((rutina) => (
                <Card key={rutina.id} className="hover:shadow-md transition-shadow">
                  <CardHeader>
                    <div className="flex items-start justify-between">
                      <div className="flex-1">
                        <CardTitle className="text-lg">{rutina.nombre}</CardTitle>
                        <CardDescription className="mt-1">
                          {rutina.descripcion || 'Sin descripción'}
                        </CardDescription>
                      </div>
                    </div>
                    <div className="flex gap-2 mt-2">
                      <Badge variant="secondary">
                        {rutina.ejercicios.length} ejercicios
                      </Badge>
                      {rutina.vecesRealizada && (
                        <Badge variant="outline">
                          {rutina.vecesRealizada} veces
                        </Badge>
                      )}
                    </div>
                  </CardHeader>
                  <CardContent>
                    <div className="space-y-3">
                      {/* Ejercicios preview */}
                      <div>
                        <p className="text-sm font-medium text-gray-700 mb-1">Ejercicios:</p>
                        <div className="text-sm text-gray-600">
                          {rutina.ejercicios.slice(0, 3).map((ej, index) => (
                            <div key={index}>• {ej.ejercicio.nombre}</div>
                          ))}
                          {rutina.ejercicios.length > 3 && (
                            <div className="text-gray-500">
                              +{rutina.ejercicios.length - 3} más...
                            </div>
                          )}
                        </div>
                      </div>

                      {/* Botones de acción */}
                      <div className="flex gap-2 pt-2">
                        <Button 
                          size="sm" 
                          className="flex-1"
                          onClick={() => marcarRutinaRealizada(rutina.id)}
                        >
                          Realizar
                        </Button>
                        <Dialog>
                          <DialogTrigger asChild>
                            <Button size="sm" variant="outline">
                              Ver
                            </Button>
                          </DialogTrigger>
                          <DialogContent className="max-w-2xl max-h-[80vh] overflow-y-auto">
                            <DialogHeader>
                              <DialogTitle>{rutina.nombre}</DialogTitle>
                              <DialogDescription>
                                {rutina.descripcion || 'Detalles de la rutina'}
                              </DialogDescription>
                            </DialogHeader>
                            <div className="space-y-4">
                              {rutina.ejercicios.map((ej, index) => (
                                <div key={index} className="border rounded-lg p-3">
                                  <h4 className="font-semibold">{ej.ejercicio.nombre}</h4>
                                  <div className="grid grid-cols-2 md:grid-cols-4 gap-2 mt-2 text-sm">
                                    <div>Series: {ej.series}</div>
                                    <div>Reps: {ej.repeticiones}</div>
                                    <div>Peso: {ej.peso} kg</div>
                                    <div>Descanso: {ej.descanso}s</div>
                                  </div>
                                  {ej.notas && (
                                    <p className="text-sm text-gray-600 mt-2">
                                      Notas: {ej.notas}
                                    </p>
                                  )}
                                </div>
                              ))}
                            </div>
                          </DialogContent>
                        </Dialog>
                      </div>

                      <div className="flex gap-2">
                        <Button 
                          size="sm" 
                          variant="outline" 
                          className="flex-1"
                          onClick={() => editarRutina(rutina)}
                        >
                          Editar
                        </Button>
                        <Button 
                          size="sm" 
                          variant="outline" 
                          onClick={() => duplicarRutina(rutina)}
                        >
                          Duplicar
                        </Button>
                        <Button 
                          size="sm" 
                          variant="destructive" 
                          onClick={() => eliminarRutina(rutina.id)}
                        >
                          Eliminar
                        </Button>
                      </div>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          ) : rutinas.length === 0 ? (
            <Card>
              <CardContent className="text-center py-12">
                <div className="text-6xl mb-4">💪</div>
                <h3 className="text-xl font-semibold mb-2">¡Comienza tu primera rutina!</h3>
                <p className="text-gray-600 mb-6">
                  Crea rutinas personalizadas con ejercicios adaptados a tus objetivos
                </p>
                <Button onClick={nuevaRutina}>
                  Crear Mi Primera Rutina
                </Button>
              </CardContent>
            </Card>
          ) : (
            <Card>
              <CardContent className="text-center py-8">
                <p className="text-gray-600">No se encontraron rutinas que coincidan con tu búsqueda</p>
              </CardContent>
            </Card>
          )}
        </TabsContent>

        <TabsContent value="sugerencias">
          <RutinasSugeridas onSeleccionar={crearRutinaDesdeTemplate} />
        </TabsContent>
      </Tabs>
    </div>
  )
}
