export const obtenerRutinaPorId = (id: string) => {
  return rutinasPredefinidas.find(rutina => rutina.id === id)
}

export const obtenerRutinasPorTipo = (tipo: string) => {
  return rutinasPredefinidas.filter(rutina => rutina.tipo === tipo)
}

export const obtenerRutinasPorNivel = (nivel: string) => {
  return rutinasPredefinidas.filter(rutina => rutina.nivel === nivel)
}
