'use client'

import { useState } from 'react'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Badge } from '@/components/ui/badge'
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select'
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog'
import { rutinasPredefinidas, type RutinaPredefinida } from '@/lib/rutinas-predefinidas'

interface RutinasSugeridasProps {
  onSeleccionar: (rutina: RutinaPredefinida) => void
}

export default function RutinasSugeridas({ onSeleccionar }: RutinasSugeridasProps) {
  const [filtroTipo, setFiltroTipo] = useState<string>('todos')
  const [filtroNivel, setFiltroNivel] = useState<string>('todos')
  const [rutinaSeleccionada, setRutinaSeleccionada] = useState<RutinaPredefinida | null>(null)

  // Filtrar rutinas
  const rutinasFiltradas = rutinasPredefinidas.filter(rutina => {
    const coincideTipo = filtroTipo === 'todos' || rutina.tipo === filtroTipo
    const coincideNivel = filtroNivel === 'todos' || rutina.nivel === filtroNivel
    return coincideTipo && coincideNivel
  })

  const obtenerColorTipo = (tipo: string) => {
    const colores = {
      hipertrofia: 'bg-blue-100 text-blue-800',
      fuerza: 'bg-red-100 text-red-800',
      resistencia: 'bg-green-100 text-green-800',
      principiante: 'bg-yellow-100 text-yellow-800'
    }
    return colores[tipo as keyof typeof colores] || 'bg-gray-100 text-gray-800'
  }

  const obtenerColorNivel = (nivel: string) => {
    const colores = {
      principiante: 'bg-green-100 text-green-800',
      intermedio: 'bg-yellow-100 text-yellow-800',
      avanzado: 'bg-red-100 text-red-800'
    }
    return colores[nivel as keyof typeof colores] || 'bg-gray-100 text-gray-800'
  }

  const convertirRutinaParaGuardar = (rutinaPredefinida: RutinaPredefinida) => {
    const ejerciciosConvertidos = rutinaPredefinida.ejercicios.flatMap(dia => 
      dia.ejercicios.map(ej => ({
        id: Date.now().toString() + Math.random().toString(),
        ejercicio: { id: ej.ejercicioId, nombre: ej.ejercicioId },
        series: ej.series,
        repeticiones: ej.repeticiones,
        peso: ej.peso,
        descanso: ej.descanso,
        notas: ej.notas || ''
      }))
    )

    return {
      id: Date.now().toString(),
      nombre: rutinaPredefinida.nombre,
      descripcion: rutinaPredefinida.descripcion,
      ejercicios: ejerciciosConvertidos,
      fechaCreacion: new Date().toISOString(),
      fechaModificacion: new Date().toISOString()
    }
  }

  return (
    <div className="space-y-6">
      {/* Filtros */}
      <Card>
        <CardHeader>
          <CardTitle>🤖 Rutinas Científicas Sugeridas</CardTitle>
          <CardDescription>
            Rutinas basadas en principios científicos de entrenamiento, diseñadas por expertos
          </CardDescription>
        </CardHeader>
        <CardContent>
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div>
              <label className="text-sm font-medium mb-2 block">Filtrar por tipo</label>
              <Select value={filtroTipo} onValueChange={setFiltroTipo}>
                <SelectTrigger>
                  <SelectValue placeholder="Todos los tipos" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="todos">Todos los tipos</SelectItem>
                  <SelectItem value="hipertrofia">Hipertrofia</SelectItem>
                  <SelectItem value="fuerza">Fuerza</SelectItem>
                  <SelectItem value="resistencia">Resistencia</SelectItem>
                  <SelectItem value="principiante">Principiante</SelectItem>
                </SelectContent>
              </Select>
            </div>
            <div>
              <label className="text-sm font-medium mb-2 block">Filtrar por nivel</label>
              <Select value={filtroNivel} onValueChange={setFiltroNivel}>
                <SelectTrigger>
                  <SelectValue placeholder="Todos los niveles" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="todos">Todos los niveles</SelectItem>
                  <SelectItem value="principiante">Principiante</SelectItem>
                  <SelectItem value="intermedio">Intermedio</SelectItem>
                  <SelectItem value="avanzado">Avanzado</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </CardContent>
      </Card>

      {/* Lista de rutinas */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
        {rutinasFiltradas.map((rutina) => (
          <Card key={rutina.id} className="hover:shadow-lg transition-shadow">
            <CardHeader>
              <div className="flex items-start justify-between">
                <div className="flex-1">
                  <CardTitle className="text-lg">{rutina.nombre}</CardTitle>
                  <CardDescription className="mt-1">
                    {rutina.descripcion}
                  </CardDescription>
                </div>
              </div>
              <div className="flex gap-2 mt-3">
                <Badge className={obtenerColorTipo(rutina.tipo)}>
                  {rutina.tipo}
                </Badge>
                <Badge className={obtenerColorNivel(rutina.nivel)}>
                  {rutina.nivel}
                </Badge>
                <Badge variant="outline">
                  {rutina.frecuencia}
                </Badge>
              </div>
            </CardHeader>
            <CardContent>
              <div className="space-y-3">
                <div>
                  <p className="text-sm font-medium text-gray-700">Split:</p>
                  <p className="text-sm text-gray-600">{rutina.split}</p>
                </div>
                
                <div>
                  <p className="text-sm font-medium text-gray-700">Duración:</p>
                  <p className="text-sm text-gray-600">{rutina.duracion}</p>
                </div>

                <div>
                  <p className="text-sm font-medium text-gray-700">Días de entrenamiento:</p>
                  <div className="text-sm text-gray-600">
                    {rutina.ejercicios.map((dia, index) => (
                      <div key={index} className="truncate">• {dia.dia}</div>
                    ))}
                  </div>
                </div>

                <div className="flex gap-2 pt-3">
                  <Dialog>
                    <DialogTrigger asChild>
                      <Button size="sm" variant="outline" className="flex-1">
                        Ver Detalles
                      </Button>
                    </DialogTrigger>
                    <DialogContent className="max-w-4xl max-h-[80vh] overflow-y-auto">
                      <DialogHeader>
                        <DialogTitle>{rutina.nombre}</DialogTitle>
                        <DialogDescription>
                          {rutina.descripcion}
                        </DialogDescription>
                      </DialogHeader>
                      <div className="space-y-6">
                        {/* Información general */}
                        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                          <div>
                            <p className="text-sm font-medium">Tipo</p>
                            <Badge className={obtenerColorTipo(rutina.tipo)}>
                              {rutina.tipo}
                            </Badge>
                          </div>
                          <div>
                            <p className="text-sm font-medium">Nivel</p>
                            <Badge className={obtenerColorNivel(rutina.nivel)}>
                              {rutina.nivel}
                            </Badge>
                          </div>
                          <div>
                            <p className="text-sm font-medium">Frecuencia</p>
                            <p className="text-sm">{rutina.frecuencia}</p>
                          </div>
                          <div>
                            <p className="text-sm font-medium">Duración</p>
                            <p className="text-sm">{rutina.duracion}</p>
                          </div>
                        </div>

                        {/* Ejercicios por día */}
                        <div>
                          <h4 className="font-semibold mb-3">Estructura de Entrenamiento</h4>
                          <div className="space-y-4">
                            {rutina.ejercicios.map((dia, diaIndex) => (
                              <Card key={diaIndex}>
                                <CardHeader className="pb-3">
                                  <CardTitle className="text-base">{dia.dia}</CardTitle>
                                </CardHeader>
                                <CardContent>
                                  <div className="space-y-2">
                                    {dia.ejercicios.map((ej, ejIndex) => (
                                      <div key={ejIndex} className="flex items-center justify-between p-2 bg-gray-50 rounded">
                                        <div className="flex-1">
                                          <p className="font-medium text-sm">{ej.ejercicioId}</p>
                                          {ej.notas && (
                                            <p className="text-xs text-gray-600">{ej.notas}</p>
                                          )}
                                        </div>
                                        <div className="text-xs text-gray-600 text-right">
                                          <div>{ej.series} series × {ej.repeticiones}</div>
                                          <div>{ej.descanso}s descanso</div>
                                        </div>
                                      </div>
                                    ))}
                                  </div>
                                </CardContent>
                              </Card>
                            ))}
                          </div>
                        </div>

                        {/* Principios científicos */}
                        <div>
                          <h4 className="font-semibold mb-2">Principios Científicos</h4>
                          <ul className="space-y-1">
                            {rutina.principiosCientificos.map((principio, index) => (
                              <li key={index} className="text-sm text-gray-600">
                                • {principio}
                              </li>
                            ))}
                          </ul>
                        </div>

                        {/* Beneficios */}
                        <div>
                          <h4 className="font-semibold mb-2">Beneficios</h4>
                          <ul className="space-y-1">
                            {rutina.beneficios.map((beneficio, index) => (
                              <li key={index} className="text-sm text-gray-600">
                                • {beneficio}
                              </li>
                            ))}
                          </ul>
                        </div>
                      </div>
                    </DialogContent>
                  </Dialog>
                  
                  <Button 
                    size="sm" 
                    className="flex-1"
                    onClick={() => {
                      const rutinaConvertida = convertirRutinaParaGuardar(rutina)
                      onSeleccionar(rutinaConvertida as any)
                    }}
                  >
                    Usar Esta Rutina
                  </Button>
                </div>
              </div>
            </CardContent>
          </Card>
        ))}
      </div>

      {rutinasFiltradas.length === 0 && (
        <Card>
          <CardContent className="text-center py-12">
            <div className="text-6xl mb-4">🔍</div>
            <h3 className="text-xl font-semibold mb-2">No se encontraron rutinas</h3>
            <p className="text-gray-600 mb-6">
              Prueba con diferentes filtros para encontrar rutinas que se adapten a ti
            </p>
            <Button onClick={() => {
              setFiltroTipo('todos')
              setFiltroNivel('todos')
            }}>
              Limpiar Filtros
            </Button>
          </CardContent>
        </Card>
      )}
    </div>
  )
}
