// Funciones de utilidad
export const obtenerEjerciciosPorCategoria = (categoria: string) => {
  return ejerciciosBase.filter(ejercicio => ejercicio.categoria === categoria)
}

export const obtenerEjerciciosPorDificultad = (dificultad: string) => {
  return ejerciciosBase.filter(ejercicio => ejercicio.dificultad === dificultad)
}

export const buscarEjercicios = (termino: string) => {
  const terminoLower = termino.toLowerCase()
  return ejerciciosBase.filter(ejercicio => 
    ejercicio.nombre.toLowerCase().includes(terminoLower) ||
    ejercicio.musculosPrincipales.some(musculo => 
      musculo.toLowerCase().includes(terminoLower)
    )
  )
}

export const obtenerEjercicioPorId = (id: string) => {
  return ejerciciosBase.find(ejercicio => ejercicio.id === id)
}
